.page-header {
  max-width: 1480px;
  margin: 0 auto;
  padding: 0 35px; }

.page-header__wrap {
  display: flex;
  justify-content: space-between;
  flex-wrap: nowrap;
  padding: 33px 0 0;
  color: #ffffff; }

.page-header__logo-wrap {
  display: flex;
  align-items: center; }

.page-header__contacts-wrap {
  display: flex;
  align-items: center; }

.page-header__title {
  position: relative;
  font-weight: 700;
  font-family: "Roboto Condensed";
  line-height: 29px;
  font-size: 23px;
  margin: -18px 0 0 10px; }

.page-header__title-description {
  display: block;
  width: 106px;
  position: absolute;
  bottom: -13px;
  left: 2px;
  font-weight: 400;
  font-family: "Roboto";
  line-height: 14.06px;
  font-size: 12px;
  color: #788891; }

.page-header__mail-link,
.page-header__phone-link {
  display: flex;
  color: #ffffff;
  font-family: "PT Mono";
  line-height: 18px;
  font-size: 16px;
  font-weight: 400; }

.page-header__mail-text {
  display: block;
  width: 96px;
  margin: 0 68px 0 20px; }

.page-header__phone-text {
  display: block;
  width: 184px;
  margin: 0 38px 0 16px; }

.page-header__button {
  display: block;
  width: 180px;
  cursor: pointer;
  background: none;
  color: #ffffff;
  border: 1px solid #ffffff;
  border-radius: 5px;
  text-transform: uppercase;
  font-family: Roboto Condensed;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  padding: 10px 0; }

@media (max-width: 1023px) {
  .page-header {
    max-width: 1023px;
    margin: 0 auto;
    padding: 0 25px; }
  .page-header__wrap {
    padding: 20px 0; }
  .page-header__mail-text,
  .page-header__phone-text,
  .page-header__button {
    display: none; }
  .page-header__mail-logo {
    width: 25px;
    height: 18px;
    margin: 0 34px 0 0; }
  .page-header__phone-logo {
    width: 24px;
    height: 24px; } }

@media (max-width: 767px) {
  .page-header {
    max-width: 767px;
    margin: 0 auto;
    padding: 0 15px; }
  .page-header__wrap {
    padding: 17px 0; }
  .page-header__title {
    display: none; }
  .page-header__logo {
    width: 38px;
    height: 38px; }
  .page-header__mail-logo {
    margin: 0 23px 0 0; } }

.intro {
  display: block;
  margin: -124px auto 0;
  background-color: #1d2e36;
  background: url("../img/bg-picture-desktop.png") 1038px 258px no-repeat, url("../img/bg-desktop.jpg") no-repeat;
  background-size: auto, cover;
  padding: 316px 0 278px 0; }

.intro__wrap {
  max-width: 1480px;
  margin: 0 auto;
  padding: 0 35px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap; }

.intro__title {
  padding: 0;
  display: block;
  font-family: Roboto Condensed;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  text-transform: uppercase;
  color: #ffffff;
  margin: 0 0 31px 0; }

.intro__desription {
  margin: 0;
  padding: 0;
  width: 683px;
  font-family: PT Mono;
  font-weight: 400;
  font-size: 22px;
  line-height: 34px;
  color: #ffffff;
  margin: 0 0 37px 98px;
  position: relative; }
  .intro__desription::before {
    content: "";
